import Router, { withRouter } from "next/router";
import {} from "./web-head-css";

const handleClick = () => {
  Router.push("/");
};

const WebHead = () => {
  return (
    <Header>
      <Inline>
        <Logo onClick={handleClick}>Practice</Logo>
        <User />
      </Inline>
    </Header>
  );
};

export default withRouter(WebHead);
